import * as React from "react"
import { useState } from "react"
import { Link } from 'gatsby'

const RESUME_LINK = "https://drive.google.com/file/d/1MXHUZqdHg27KzLerJZKD_4DZnVi6fEeC/view"

const titleStyle = {
    fontFamily: "Poppins",
    fontWeight: 700,
    fontSize: 15,
    flexGrow: 0,
    textDecoration: "none",
    color: "#000000",
}
const titleStyleNarrow = {
    marginLeft: 30
}
const titleStyleWide = {
    marginLeft: 90
}
const containerStyle = {
    display: "flex",
    flexDirection: "horizontal",
    height: "160px",
    alignItems: "center",
    justifyContent: "space-between",
    flexGrow: 1,
    borderBottomStyle: "solid",
    borderWidth: 1
}
const navigatorStyle = {
    display: "flex",
    flexGrow: 0,
    marginRight: "50px"
}
const navigatorStyleWide = {
    flexDirection: "row",
    gap: "10px 60px",
}
const navigatorStyleNarrow = {
    flexDirection: "column",
    gap: "0px 0px",
}
const linkStyle = {
    fontFamily: "Poppins",
    fontSize: 14,
    fontWeight: 400,
    textDecoration: "none",
    color: "#000000"
}
const blueText = {
    color: "#4F89D6"
}

export default function Header(props) {
    const [hoverTitle, setHoverTitle] = useState("")
    return (
        <main style={containerStyle}>
            <Link to="/" style={{
                ...titleStyle,
                ...props.narrowWindow ? titleStyleNarrow : titleStyleWide}}
            >ashley wong</Link>
            <div style={{...navigatorStyle, ...props.narrowWindow ? navigatorStyleNarrow : navigatorStyleWide}}>
                <Link to="/" 
                    style={{...linkStyle, ...hoverTitle==="projects" ? blueText : {}}}
                    onMouseOver={() => setHoverTitle("projects")}
                    onMouseLeave={() => setHoverTitle("")}
                >projects</Link>
                <Link to={RESUME_LINK} 
                    style={{...linkStyle, ...hoverTitle==="resume" ? blueText : {}}}
                    onMouseOver={() => setHoverTitle("resume")}
                    onMouseLeave={() => setHoverTitle("")}
                    target="_blank"
                >resume</Link>
                <Link to="/about" 
                    style={{...linkStyle, ...hoverTitle==="about" ? blueText : {}}}
                    onMouseOver={() => setHoverTitle("about")}
                    onMouseLeave={() => setHoverTitle("")}
                >about</Link>
            </div>
        </main>
    )
}
