import * as React from "react"
import LeftBox from "../components/left_box"
import RightBox from "../components/right_box"
import AW from "../images/AW.svg"
import { Link } from 'gatsby'
import { StaticImage } from "gatsby-plugin-image"

const blockStyle = {
  display: "flex",
  alignItems: "center",
  flexGrow: 0,
  width: "100%",
  borderBottomStyle: "solid",
  borderWidth: 1
}
const containerStyle = {
  display: "flex",
  alignItems: "center",
  flexShrink: 1,
  flexGrow: 1,
  justifyContent: "left"
}
const titleStyle = {
  fontFamily: "Poppins",
  fontWeight: 600,
  flexGrow: 0,
  flexShrink: 0,
  whiteSpace: "nowrap",
  overflow: "hidden"
}
const titleStyleNarrow = {
  textAlign: "left",
  alignSelf: "stretch",
  paddingLeft: "30px",
  marginBottom: 10,
  marginTop: 55,
  fontSize: "20px",
  letterSpacing: "0.07em"
}
const titleStyleWide = {
  fontSize: "min(max(2.35vw, 35px),50px)",
  margin: "20px 0px 20px 95px",
  letterSpacing: "0.16em",
}
const bodyStyle = {
  fontFamily: "Poppins",
  fontWeight: 400,
  flexGrow: 0,
}
const bodyStyleNarrow = {
  fontSize: "12px",
  margin: "0px 30px 36px 30px"
}
const bodyStyleWide = {
  fontSize: "min(max(1.5vw, 20px),30px)",
  margin: "20px 50px 20px 8vw",
  maxWidth: "800px",
}
const logoStyle = {
  flexGrow: 0,
  margin: 20
}
const boxStyle = {
  display: "flex",
  alignSelf: "stretch",
  alignItems: "center",
  justifyContent: "space-between",
  flexDirection: "row",
  borderTopStyle: "solid",
  borderWidth: 1,
  flexGrow: 0,
  flexShrink: 0,
  padding: "20px 15px 20px 15px"
}

const links = [
  <Link to="https://www.google.ca">
    <StaticImage src="../images/instagram.png" style={logoStyle} alt="instagram"/>
  </Link>,
  <Link to="https://www.google.ca/">
    <StaticImage src="../images/linkedin.png" style={logoStyle} alt="linkedin"/>
  </Link>,
  <Link to="https://www.google.ca">
    <StaticImage src="../images/email.png" style={logoStyle} alt="email"/>
  </Link>
]

export default function AboutSnippet(props) {
  return (
    <main style={{...blockStyle, ...props.narrowWindow ? {} : {minHeight: 500}}}>
      <LeftBox narrowWindow={props.narrowWindow}>
        <AW style={{
          ...logoStyle,
          ...props.narrowWindow?{height: 55}:{}}}/>
      </LeftBox>
      <div style={{...containerStyle, ...props.narrowWindow ? 
        {flexDirection: "column"} : {flexDirection: "row"}}}>
        <h1 style={{...titleStyle, ...props.narrowWindow ? titleStyleNarrow : titleStyleWide}}>
          Hi there, {props.narrowWindow ? <></> : <br/>}I'm Ashley!
          </h1>
        <p1 style={{...bodyStyle, ...props.narrowWindow ? bodyStyleNarrow : bodyStyleWide}}>I am a UI and graphic designer who finds beauty in simplicity. 
          I strive to create meaningful designs and products that provide effective solutions.</p1>
        { props.narrowWindow ? <div style={boxStyle}>{links}</div> : <div/>}
      </div>
      
      {
        props.narrowWindow ? <div/> : <RightBox>{links}</RightBox>
      }
      
      
    </main>
  )
}

