import * as React from "react"
import getImage from "../helpers/get_image"

const containerStyle = {
    position: "relative"
}

const imageStyle = {
    width: "40%",
    height: "calc(100% - 1px)",
    position: "absolute",
    zIndex: 10,
    top: -1,
    right: 0,
    borderStyle: "solid",
    borderTopStyle: "solid",
    borderWidth: 1
}

export default function ImageSection(props) {
    return (
        <div style={containerStyle}>
            {
                props.narrowWindow || !props.supportedTitles.includes(props.currentTitle) ? <div/> : 
                <div style={imageStyle}>
                    { getImage(props.currentTitle) }
                </div>
            }
            {props.children}
        </div>
    )
}
  