import * as React from "react"
import CustomImage from "../components/custom_image"
import * as Constants from "../constants"

const imageStyle = {
    height: "100%",
    width: "100%"
}

export default function getImage(title) {
    var imageSrc = ""
    var imageAlt = ""
    var background = "#DEE3E8"
    switch(title) {
        case Constants.CP_COMPANY:
            imageSrc = "CPC_landing.png"
            imageAlt = "Picture of CP Company"
            background = "#DEE3E8"
            break;
        case Constants.SWISS_FILMS:
            imageSrc = "SF_landing.gif"
            imageAlt = "Picture of Swiss Films"
            background = "#000000"
            break;
        case Constants.EXPERIMENTATION:
            imageSrc = "Exp_landing.png"
            imageAlt = "Picture of Experiments"
            background = "#F6F0E7"
            break;
        case Constants.YORK_HOUSE_SCHOOL:
            imageSrc = "YHS_landing.png"
            imageAlt = "Picture of York House School"
            background = "#F6F0E7"
            break;
        case Constants.AKWSTUDIOS:
            imageSrc = "AKW_landing.gif"
            imageAlt = "Picture of Taro"
            background = "#DEE3E8"
            break;
        case Constants.PHOTOGRAPHY:
            imageSrc = "PH_landing.png"
            imageAlt = "Picture of Photography"
            background = "#000000"
            break;
        default:
            imageSrc = "cp_ideation.png"
            imageAlt = ""
            break;
    }
    return <CustomImage
                imageName={imageSrc}
                alt={imageAlt}
                style={{...imageStyle, 
                        ...{background: background}
                }}
                objectFit={"contain"}
            />
}