import { graphql, useStaticQuery } from "gatsby"
import { GatsbyImage } from "gatsby-plugin-image"
import * as React from "react"

const containerStyle = {
    display: "flex",
    flexShrink: 1,
    flexGrow: 0,
}
const imageStyle = {
    height: "100%",
    width: "100%",
    objectFit: "contain",
    alignSelf: "stretch"
}

function isGif(imageName) {
    if (typeof imageName === 'undefined') return false
    else return imageName.toLowerCase().endsWith(".gif")
}

export default function CustomImage(props) {
    const data = useStaticQuery(graphql`
        query {
            allFile {
            nodes {
                childrenImageSharp {
                gatsbyImageData
                }
                name
            }
            }
        }
    `)
    const imageNode = data.allFile.nodes.find(node => node.name === props.imageName.replace(/\.[^/.]+$/, ""))
    const image = imageNode?.childrenImageSharp[0]?.gatsbyImageData
    // console.log(props.imageName.replace(/\.[^/.]+$/, ""))
    // console.log(image)
    return (
        <div style={props.style}>
            {
                isGif(props.imageName) ? 
                <img src={props.imageName} style={{...imageStyle, ...props.style}}/>
                :
                <GatsbyImage image={image} alt={props.alt}
                    style={{...imageStyle}}
                    imgStyle={props.imageStyle}
                    objectPosition={props.objectPosition}
                    objectFit={props.objectFit}
                />
            }
        </div>
    )
}

CustomImage.defaultProps = {
    objectFit: "cover",
    objectPosition: "center"
}