import * as React from "react"
import LeftBox from "../components/left_box"

const blockStyle = {
  display: "flex",
  alignItems: "center",
  flexGrow: 1,
  width: "100%",
  borderBottomStyle: "solid",
  borderWidth: 1
}
const titleStyle = {
  fontFamily: "Poppins",
  flexGrow: 1,
  flexShrink: 0,
}
const titleStyleNarrow = {
  fontSize: 15,
  fontWeight: 600,
  letterSpacing: "0.24em",
  margin: "15px 10px 15px 30px"
}
const titleStyleWide = {
  fontSize: 20,
  fontWeight: 600,
  letterSpacing: "0.24em",
  margin: "30px 100px 30px 95px"
}
const blueText = {
  color: "#4F89D6"
}
const blackText = {
  color: "#000000"
}

function isHovered(props) {
  if (props.supportedTitles == null) return false
  return props.supportedTitles.includes(props.currentTitle) === true
}

export default function SectionHeader(props) {
  return (
    <main style={blockStyle}>
      <LeftBox narrowWindow={props.narrowWindow} isHovered={isHovered(props)}/>
      <h1 style={{
        ...titleStyle,
        ...props.narrowWindow ? titleStyleNarrow : titleStyleWide,
        ...isHovered(props) ? blueText : blackText}
        }>{props.title}</h1>
    </main>
  )
}

